@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  font-family: "Roboto", sans-serif;
}

body {
  background-color: #182b55;
  color: white;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
}

main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
}
section {
  padding-bottom: 100vh;
}
.headerContainer {
  z-index: 2;
  width: 100%;
  height: 60px;
  display: -webkit-flex;
  display: flex;
  position: fixed;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  background-color: #132344;
}
.logo {
  font-size: 40px;
  padding-left: 25px;
  background-color: #132344;
  border-style: none;
  cursor: pointer;
  color: rgb(226, 226, 226);
  outline: none;
}
.headerSearch input {
  box-sizing: content-box;
  border-style: none;
  height: 40px;
  font-size: 15px;
  width: 195px;
  color: rgb(226, 226, 226);
  border-radius: 30px;
  text-align: center;
  background-color: #0c1629;
  margin-left: 30px;
  padding: 0 20px;
  text-align: start;
  outline: none;
}
.headerSearch input:focus {
  box-shadow: 0 0 5px 3px #55a7f3;
  transition: all ease 0.3s;
}
.headerSearch button {
  position: absolute;
  border-style: none;
  top: 18px;
  left: 300px;
  background-color: #0c1629;
  color: rgb(226, 226, 226);
  cursor: pointer;
  font-size: 20px;
  outline: none;
}
.headerSearch button :hover {
  color: rgb(221, 58, 58);
}

aside {
  background-color: #0f1b35;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-width: 90px;
  box-shadow: 2px 1px 5px #111113;
  z-index: 1;
}
.navContainer {
  display: -webkit-flex;
  display: flex;
  margin-top: 80px;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.navContainer div {
  text-align: center;
}
.navContainer button {
  background-color: #0f1b35;
  color: #55a7f3;
  border-style: none;
  font-size: 16px;
  outline: none;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap;
  -webkit-align-items: center;
          align-items: center;
  margin: 20px 0;
}
.navContainer button:active {
  background-color: #1c305c;
  border-radius: 10px;
}

.navContainer button path {
  margin: 10px;
}
.navContainer button div {
  color: rgb(226, 226, 226);
  padding-top: 5px;
}
.navContainer svg {
  font-size: 45px;
}
.movieContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: center;
          justify-content: center;
  padding-bottom: 30px;
}
.currentPage {
  width: 100%;
  font-size: 30px;
  text-align: left;
  margin: 100px 0 30px 30px;
}

.movies {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  max-width: 200px;
  height: 300px;
  cursor: pointer;
  text-align: left;
  margin: 50px 20px;
}
.moviePoster:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: all ease 0.5s;
}
.releaseDate {
  font-size: 16px;
  color: #c4c2c2;
  margin: 10px 0 5px;
}
.movieTitle {
  width: 100%;
  font-size: 16px;
}
.moviePoster {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
  border-radius: 5px;
  box-shadow: 6px 6px 14px rgb(0 0 0 / 42%);
}
.emptyHeart {
  border-style: none;
  background: none;
  color: white;
  position: absolute;
  top: 13px;
  left: 10px;
  font-size: 40px;
  outline: none;
  cursor: pointer;
}
.fillHeart {
  border-style: none;
  background: none;
  color: white;
  position: absolute;
  top: 6px;
  left: 10px;
  font-size: 42px;
  outline: none;
  cursor: pointer;
}
.closeBtn {
  position: absolute;
  top: 17px;
  right: 23px;
  font-size: 30px;
  background: none;
  color: white;
  border-style: none;
  cursor: pointer;
  border-radius: 30px;
  outline: none;
}
.closeBtn:active {
  color: rgb(221, 58, 58);
}
.deleteButton {
  margin-top: 5px;
  font-size: 30px;
  background: none;
  border-style: none;
  outline: none;
  cursor: pointer;
  color: white;
  position: relative;
  left: 156px;
}
.detailContainer {
  display: -webkit-flex;
  display: flex;
  height: 100vh;
  margin-top: 60px;
  -webkit-justify-content: center;
          justify-content: center;
  background: fixed;
  top: 0;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.backdropContainer {
  width: 100%;
}
.backdropImg {
  width: 100%;
  top: 10px;
  height: 50vh;
  object-fit: cover;
  opacity: 0.2;
}
/* .backShadow {
  box-shadow: inset 0 -0px 95px 99px #182b55;
  height: 50vh;
  position: absolute;
  top: 60px;
  width: 91.4%;
} */
.movieDetail {
  display: -webkit-flex;
  display: flex;
  position: relative;
  background-color: rgb(8, 15, 29);
  width: 1100px;
  padding: 40px;
  border-radius: 7px;
  -webkit-align-items: center;
          align-items: center;
  bottom: 30vh;
}

.detailImg {
  width: 350px;
  height: 515px;
  object-fit: cover;
  border-radius: 5px;
  box-shadow: 6px 6px 14px rgb(0 0 0 / 42%);
}
.detailInfo {
  display: inline-block;
  height: inherit;
  padding: 0 50px;
}
.detailTitle {
  font-size: 40px;
  padding-bottom: 20px;
}
.detailGenres {
  font-size: 18px;
  padding-bottom: 10px;
  color: #b8b8b8;
}
.detailRuntime {
  font-size: 18px;
  padding-bottom: 10px;
  color: #b8b8b8;
}
.detailDate {
  font-size: 18px;
  padding-bottom: 20px;
  color: #b8b8b8;
}
.detailVote {
  padding-bottom: 35px;
}
.detailOverviewTitle {
  font-size: 24px;
  padding-bottom: 10px;
}
.detailOverview {
  font-size: 20px;
  line-height: 30px;
}

.modalDiv {
  position: fixed;
  background-color: rgb(255, 86, 86);
  top: -100px;
  right: 30px;
  cursor: pointer;
  height: 100px;
  width: 300px;
  text-align: center;
  line-height: 100px;
  border-radius: 10px;
  transition-duration: 0.5s;
}
.modalActive {
  -webkit-transform: translateY(200px);
          transform: translateY(200px);
  transition-timing-function: ease-in;
}
.modalText {
  font-size: 24px;
}
@media screen and (max-width: 1290px) {
  aside {
    min-width: 70px;
  }
  .navContainer button {
    margin: 10px 5px;
    background-color: #0f1b35;
    color: #55a7f3;
    border-style: none;
    font-size: 12px;
    outline: none;
    cursor: pointer;
  }
  .movieDetail {
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    background-color: rgba(255, 255, 255, 0);
    -webkit-align-items: flex-start;
            align-items: flex-start;
    padding: 20px;
    bottom: 46vh;
  }
  .navContainer svg {
    font-size: 35px;
  }
  .detailImg {
    width: 240px;
    height: 300px;
    border-radius: 5px;
    box-shadow: 6px 6px 14px rgb(0 0 0 / 42%);
  }
  .detailInfo {
    padding: 0;
  }
  .emptyHeart {
    top: -25px;
    left: 10px;
  }
  .fillHeart {
    top: -32px;
    left: 10px;
  }
}
@media screen and (max-width: 600px) {
  .closeBtn {
    position: absolute;
    top: -24px;
    right: 23px;
    font-size: 30px;
    background: none;
    color: white;
    border-style: none;
    cursor: pointer;
    border-radius: 30px;
    outline: none;
  }
}

